@font-face {
  font-family: Fredoka;
  src: url("Fredoka.d7ec6ee1.ttf");
}

:root {
  color-scheme: light dark;
  font-synthesis: none;
  text-rendering: optimizelegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  font-size: 16px;
  font-weight: 400;
}

*, :before, :after {
  margin: 0;
  padding: 0;
}

body {
  min-width: 320px;
  min-height: 100vh;
  scroll-behavior: smooth;
}

body::-webkit-scrollbar {
  appearance: none;
  width: 6px;
}

body::-webkit-scrollbar-thumb {
  background: #ff880040;
}

#app {
  width: 100vw;
  height: 100%;
  max-width: 1280px;
  text-align: center;
}

.w-full {
  width: 100%;
}

.h-full {
  height: 100%;
}

.w-fit {
  width: -moz-fit-content;
  width: fit-content;
}

.h-fit {
  height: -moz-fit-content;
  height: fit-content;
}

h1, h2, h3, h4, h5, h6 {
  font-family: Fredoka, sans-serif;
}

li {
  list-style: none;
}

a {
  color: inherit;
  cursor: pointer;
  text-decoration: none;
}

button {
  color: inherit;
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
}

.title, .subtitle {
  color: #f80;
  text-shadow: -2px 1px #ff880080;
}

.d-none {
  display: none;
}

.d-block {
  display: block;
}

.d-inline {
  display: inline;
}

.d-inline-block {
  display: inline-block;
}

.d-flex {
  display: flex;
}

.flex-column {
  flex-direction: column;
}

.justify-start {
  justify-content: flex-start;
}

.justify-end {
  justify-content: flex-end;
}

.justify-center {
  justify-content: center;
}

.justify-between {
  justify-content: space-between;
}

.items-center {
  align-items: center;
}

.container {
  margin: 0 auto;
}

header {
  width: 100%;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  z-index: 1;
  padding: 5px auto;
  position: sticky;
  top: 0;
  left: 0;
}

main {
  width: 100%;
}

#heading {
  height: -moz-fit-content;
  height: fit-content;
}

#header-container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

#sidebar-show-btn {
  display: none;
}

#header-nav {
  margin-right: 15px;
}

#header-nav ul li {
  padding: 0 5px;
}

#sidebar-nav ul li {
  padding: 5px;
}

#header-nav ul li a {
  padding: 10px 8px;
}

#sidebar-nav ul li a {
  padding: 10px;
  font-size: 18px;
}

#theme-toggle {
  width: 68px;
  height: 32px;
  border-radius: 22px;
  justify-content: space-between;
  justify-items: center;
  padding: 1px 4px;
  display: flex;
  position: relative;
}

#theme-toggle button {
  padding: 1px;
}

#toggle-circle {
  width: 30px;
  height: 30px;
  z-index: -1;
  background: #ddd;
  border-radius: 17px;
  position: absolute;
  top: 2px;
  left: 2px;
}

#theme-toggle button:first-of-type {
  color: #f80;
}

#theme-toggle button:last-of-type {
  color: #505050;
}

.dark-theme-toggle {
  animation: .5s forwards switchToDark;
}

.light-theme-toggle {
  animation: .5s forwards switchToLight;
}

#sun-svg {
  width: 24px;
  height: 24px;
}

#moon-svg {
  width: 20px;
  height: 20px;
}

#mobile-sidebar {
  width: 100%;
  height: 100vh;
  filter: blur(.5px);
  position: fixed;
  top: 0;
  left: 0;
}

#mobile-sidebar-container {
  width: 75%;
  height: 100%;
  position: absolute;
  top: 0;
  left: -100%;
}

#mobile-sidebar-header {
  padding: 6.5px 0;
}

#sidebar-hide-btn {
  border-radius: 5px;
  margin-right: 5px;
  padding: 3px;
}

#mobile-sidebar-body {
  width: 100%;
  height: calc(100% - 45px);
}

#mobile-sidebar-body #sidebar-nav {
  text-align: center;
  padding-top: 24px;
}

#mobile-sidebar-body #footer {
  width: 100%;
  height: 32px;
  text-align: center;
  font-size: 12px;
  font-weight: lighter;
}

.hide {
  display: none;
}

.show {
  display: block;
}

.from-left-anim {
  animation: .25s ease-out .1s forwards fromLeft;
}

.from-right-anim {
  animation: .25s ease-out forwards fromRight;
}

#hero-section {
  width: 100%;
  align-items: center;
  padding-top: 35px;
  display: flex;
}

#hero-section section:first-of-type {
  width: 50%;
  height: 400px;
  text-align: start;
}

#hero-section section:first-of-type h3 {
  font-size: 32px;
}

#hero-section section:first-of-type h2 {
  padding-top: 7px;
  padding-bottom: 7px;
  font-size: 50px;
}

#hero-section section:first-of-type h1 {
  color: #f80;
  text-shadow: -2px 1px #ff880080;
  font-size: 64px;
}

#hero-section section:first-of-type h1:first-of-type {
  margin-top: 24px;
}

#hero-section section:first-of-type #wido {
  width: 75%;
  margin-top: 18px;
  margin-bottom: 18px;
  font-size: 18px;
}

#hero-section section:first-of-type #medias-icons {
  width: -moz-fit-content;
  width: fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin-top: 25px;
}

#hero-section section:first-of-type #medias-icons a {
  padding: 5px;
}

#hero-section section:first-of-type #medias-icons a .media-svg {
  color: #ff8800bf;
  font-size: 32px;
}

#hero-section section:last-of-type {
  width: 50%;
  justify-content: end;
  align-items: center;
  display: flex;
}

#hero-hexagones {
  width: 80%;
  height: -moz-fit-content;
  height: fit-content;
  overflow-x: hidden;
}

#hero-hexagones polygon {
  stroke-width: 5px;
}

#scroll-btn-container {
  width: 40px;
  height: -moz-fit-content;
  height: fit-content;
  margin: 24px auto;
}

#about, #projects, #contact-me, #resume {
  padding-top: 60px;
}

#about h1, #projects h1, #contact-me h1, #resume h1 {
  width: -moz-fit-content;
  width: fit-content;
  font-size: 50px;
}

#about .subtitle, #projects .subtitle, #contact-me .subtitle, #resume .subtitle {
  margin-top: 30px;
  margin-bottom: 30px;
  font-size: 40px;
}

#about .content, #projects .content, #contact-me .content, #resume .content {
  width: 100%;
  justify-content: space-around;
  display: flex;
}

#about-me {
  background: url("background.56817322.png") 100% no-repeat;
}

#about-me .text {
  -webkit-backdrop-filter: blur(5px);
  backdrop-filter: blur(5px);
}

#about-me, #tech, #tools, #experience {
  width: 80%;
  margin: 24px auto;
}

#about-me p:nth-of-type(2), #tech p:nth-of-type(2), #tools p:nth-of-type(2), #experience p:nth-of-type(2) {
  margin: 16px auto;
}

#about-me .content, #tech .content, #tools .content, #experience .content {
  padding-top: 20px;
  padding-bottom: 20px;
}

#about-me .img-container {
  width: -moz-fit-content;
  width: fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

#about-me .img-container img {
  width: 350px;
  border: 10px solid;
  border-image-source: linear-gradient(to bottom right, #ff880040, #f80);
  border-image-slice: 1;
  box-shadow: -5px 4px #ff880080;
}

#about .text {
  width: 50%;
  text-align: justify;
  font-size: 17px;
}

#tech h3, #tools h3, #experience h3, #projects .subtitle, #contact-me .subtitle, #resume .subtitle {
  text-align: center;
  font-size: 32px;
}

#tech .content {
  width: 100%;
  grid-template-rows: 2px;
  grid-template-columns: repeat(9, 1fr);
  gap: 0;
  margin: 24px auto;
  padding-top: 12px;
  display: grid;
}

#tech .content div {
  justify-content: center;
  align-items: center;
  padding: 40px 20px;
  font-size: 70px;
  display: flex;
}

#tech .content iconify-icon[icon="simple-icons:php"] {
  color: #6181b6;
}

#tech .content iconify-icon[icon="bi:git"] {
  color: #f34f29;
}

#tech .content iconify-icon[icon*="logos:"] {
  font-size: 60px;
}

#tech .content .tech-row-span-2 {
  grid-row: span 2;
}

#tools .content {
  width: -moz-fit-content;
  width: fit-content;
  margin: 50px auto;
  display: block;
}

#tools .content .row {
  grid-template-columns: repeat(3, 1fr);
  display: grid;
  width: 100% !important;
}

#tools .content .row div {
  width: -moz-fit-content;
  width: fit-content;
  height: -moz-fit-content;
  height: fit-content;
  justify-content: center;
  align-items: center;
  font-size: 60px;
  display: flex;
}

#tools .content .row div .hexagon {
  width: 175px;
  height: 175px;
  stroke-width: 20px !important;
}

#tools .content .row div image {
  width: 100px;
  height: 100px;
  background-color: #0073b1 !important;
}

#tools .content .row:first-of-type div:nth-child(1) svg {
  stroke: #2489ca;
}

#tools .content .row:first-of-type div:nth-child(2) svg {
  stroke: #000;
}

#tools .content .row:first-of-type div:nth-child(3) svg {
  stroke: #fcd209;
}

#tools .content .row:last-of-type div:nth-child(1) svg {
  stroke: #9013fe !important;
}

#tools .content .row:last-of-type div:nth-child(2) svg {
  stroke: #ff6c37 !important;
}

#tools .content .row:last-of-type div:nth-child(3) svg {
  stroke: #df731b !important;
}

#experience .content {
  width: 100%;
  flex-direction: column;
  margin-top: 2rem;
  margin-bottom: 1rem;
  display: flex;
}

#experience .content .row {
  width: -moz-fit-content;
  width: fit-content;
  height: -moz-fit-content;
  height: fit-content;
  flex-direction: column;
  justify-content: center;
  margin-top: 1rem;
  display: flex;
  position: relative;
}

#experience .content .row .desc {
  width: 50%;
  padding-top: 10px;
  padding-left: 4.7rem;
}

.company-logo {
  align-items: center;
  display: flex;
}

.company-logo img {
  width: 50px;
  height: 50px;
  border: 5px solid #ff88009a;
  border-radius: 3rem;
  padding: 2px;
  box-shadow: 0 0 0 3px #f80;
}

.company-logo div h2 {
  color: #ff8800bf;
  margin-left: 10px;
  font-size: 32px;
}

.company-logo div p {
  color: #f80;
  margin-left: 10px;
  font-weight: bold;
}

#experience .content .row:before {
  content: "";
  width: 10px;
  height: 100%;
  z-index: -1;
  background: linear-gradient(#ff8800bf, #ff880040);
  position: absolute;
  top: 20px;
  left: 25px;
}

#experience .content .row:last-of-type:before {
  background: linear-gradient(#ff8800bf, #f800);
}

#projects .content {
  width: 80%;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
  margin: auto;
  padding-top: 45px;
  padding-bottom: 45px;
  display: grid;
}

#projects .subtitle, #contact-me .subtitle, #resume .subtitle {
  text-shadow: none;
}

.card {
  min-height: 284px;
  border: 5px solid #ff880080;
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-between;
  padding: 15px;
  transition: box-shadow .4s;
  display: flex;
  box-shadow: -5px 4px #f80;
}

.card:hover {
  box-shadow: none;
}

.card-header {
  width: 100%;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card-header iconify-icon {
  font-size: 32px !important;
}

.card-body {
  min-height: 120px;
  flex-direction: column;
  justify-content: space-between;
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
}

.card-body h3 {
  width: -moz-fit-content;
  width: fit-content;
  margin-bottom: 10px;
  font-size: 21px !important;
}

.card-body p {
  font-size: 16px;
}

.card-footer small {
  padding: 5px 10px;
}

#see-more {
  text-align: center;
  margin-top: 30px;
  margin-bottom: 30px;
}

#see-more a, #get-in-touch a, #resume .content a {
  background: #ff880080;
  border-bottom: 1px solid #ff8800d9;
  border-left: 1px solid #ff8800d9;
  border-radius: 5px;
  padding: 12px 38px;
  font-size: 18px;
  transition: all .4s;
  box-shadow: -4px 3px #ff8800bf;
}

#see-more a:hover, #get-in-touch a:hover, #resume .content a:hover {
  box-shadow: none;
  border-bottom: 1px solid #0000 !important;
  border-left: 1px solid #0000 !important;
}

#contact-me .content {
  display: block;
}

#contact-me .content #get-in-touch, #contact-me .content #write-on-medias {
  text-align: center;
}

#contact-me .content #write-on-medias .subtitle {
  text-align: center !important;
}

#contact-me .content #write-on-medias #write-on-medias-icons {
  color: #ff8800bf;
  font-size: 40px;
}

#contact-me .content #write-on-medias #write-on-medias-icons iconify-icon {
  padding: 10px;
}

#contact-me .subtitle, #resume .subtitle {
  margin: 40px auto;
  width: 60% !important;
}

#get-in-touch a, #resume .content a {
  padding: 15px 40px;
}

#contact-me .content, #resume .content {
  padding-top: 40px !important;
  padding-bottom: 40px !important;
}

footer {
  text-align: center;
  background-color: #ff880080;
  padding-top: 20px;
  padding-bottom: 20px;
  font-size: 14px;
}

body[data-theme="light"] {
  color: #080808;
  background-color: #fffbf7;
}

body[data-theme="light"] header {
  background-color: #fffbf780;
  border-bottom: .25px solid #eee;
}

body[data-theme="light"] #theme-toggle {
  background: #eeeeee80;
  border: .75px solid #eee;
}

body[data-theme="light"] #mobile-sidebar {
  background: #9b8d80bf;
}

body[data-theme="light"] #mobile-sidebar-container {
  background: #fffbf7;
}

body[data-theme="light"] #mobile-sidebar-header {
  border-bottom: .25px solid #eee;
}

body[data-theme="light"] #sidebar-hide-btn {
  background-color: #eee;
}

body[data-theme="light"] .bg-anim-show {
  animation: .25s ease-out forwards lsbgoup;
}

body[data-theme="light"] .bg-anim-hide {
  animation: .15s ease-out 50ms forwards lsbgodown;
}

body[data-theme="light"] .company-logo img {
  background: #fffbf7;
}

body[data-theme="light"] #projects h3, body[data-theme="light"] #contact-me h3, body[data-theme="light"] #resume h3 {
  color: #080808;
}

body[data-theme="light"] #loader {
  background: #fffbf7;
}

#first-hexagone {
  width: 400px;
  height: 400px;
}

polygon {
  stroke-width: 6px;
}

body[data-theme="dark"] {
  color: #eee;
  background-color: #242424;
}

body[data-theme="dark"] header {
  background-color: #24242480;
  border-bottom: .25px solid #3d3d3d;
}

body[data-theme="dark"] #theme-toggle {
  background: #46464680;
  border: .75px solid #3d3d3d;
}

body[data-theme="dark"] #mobile-sidebar {
  background: #5f5f5f80;
}

body[data-theme="dark"] #mobile-sidebar-container {
  background: #242424;
}

body[data-theme="dark"] #mobile-sidebar-header {
  border-bottom: .25px solid #464646;
}

body[data-theme="dark"] #sidebar-hide-btn {
  background-color: #464646;
}

body[data-theme="dark"] .bg-anim-show {
  animation: .25s ease-out forwards dsbgoup;
}

body[data-theme="dark"] .bg-anim-hide {
  animation: .15s ease-out 50ms forwards dsbgodown;
}

body[data-theme="dark"] #github-link {
  color: #eee !important;
}

body[data-theme="dark"] #tools div:nth-child(2) svg {
  stroke: #eee !important;
}

body[data-theme="dark"] .company-logo img {
  background: #242424;
}

body[data-theme="dark"] #projects h3, body[data-theme="dark"] #contact-me h3, body[data-theme="dark"] #resume h3 {
  color: #eee;
}

body[data-theme="dark"] #see-more a, body[data-theme="dark"] #get-in-touch a, body[data-theme="dark"] #resume a {
  background: #ff8800bf;
  border-bottom: 1px solid #ff8800a6;
  border-left: 1px solid #ff8800a6;
  box-shadow: -4px 3px #ff880080;
}

body[data-theme="dark"] footer {
  background-color: #ff8800bf;
}

body[data-theme="dark"] #loader {
  background: #242424;
}

@media (max-width: 1280px) {
  .container {
    width: 1024px;
  }
}

@media (min-width: 1281px) {
  .container {
    width: 1280px;
  }
}

@media (max-width: 1024px) {
  .container {
    width: 90%;
  }

  #hero-section h3 {
    font-size: 28px !important;
  }

  #hero-section h2 {
    font-size: 42px !important;
  }

  #hero-section h1 {
    font-size: 50px !important;
  }

  #about-me, #tech, #tools {
    width: 100%;
  }

  #about-me .text {
    font-size: 18px;
  }

  .row .desc {
    width: 75% !important;
  }
}

@media (max-width: 992px) {
  #hero-section h3 {
    font-size: 25px !important;
  }

  #hero-section h2 {
    font-size: 36px !important;
  }

  #hero-section h1 {
    font-size: 45px !important;
  }

  #hero-section #tech h3, #hero-section #tools h3, #hero-section #experience h3 {
    font-size: 28px;
  }

  #about-me {
    width: 100%;
  }

  #about-me .text {
    font-size: 16px;
  }

  #about-me .img-container img {
    width: 250px;
  }

  #about-me #tools .content {
    margin: 0 !important;
  }

  #tech .content {
    grid-template-columns: repeat(7, 1fr);
  }

  #tech .content div {
    padding: 20px 10px;
  }

  #tech .content .tech-row-span-2 {
    grid-row: span 1;
  }

  #projects .content {
    grid-template-columns: repeat(2, 1fr) !important;
  }

  .subtitle {
    font-size: 32px !important;
  }
}

@media (max-width: 767px) {
  .container {
    width: calc(100% - 40px);
  }

  .menu-svg {
    width: 28px;
    height: 28px;
  }

  #sidebar-show-btn {
    display: block;
  }

  #header-nav {
    display: none;
  }

  header {
    width: 100% !important;
  }

  #hero-section {
    flex-direction: column;
    justify-content: center;
  }

  #hero-section section {
    margin: auto;
    width: calc(100% - 42px) !important;
  }

  #hero-section h3 {
    font-size: 28px !important;
  }

  #hero-section h2 {
    font-size: 45px !important;
  }

  #hero-section h1 {
    font-size: 64px !important;
  }

  #hero-section h1, #hero-section h2, #hero-section h3 {
    width: -moz-fit-content !important;
    width: fit-content !important;
  }

  #hero-section section:last-of-type {
    display: none !important;
  }

  #about-me .content {
    flex-direction: column;
    align-items: center;
  }

  #about-me .content .img-container img {
    width: 400px;
  }

  #about-me .content .text {
    width: calc(100% - 100px);
    margin-top: 25px;
    font-size: 16px;
  }

  #tech .content iconify-icon {
    font-size: 60px;
  }

  #tech .content iconify-icon[icon*="logos:"] {
    font-size: 50px;
  }

  #tools .content .hexagon {
    width: 150px !important;
    height: 150px !important;
  }

  #experience .content .row .desc {
    width: 80% !important;
  }

  .company-logo img {
    width: 45px !important;
    height: 45px !important;
  }

  #projects .content {
    width: 100% !important;
  }
}

@media (max-width: 640px) {
  .container {
    width: calc(100% - 20px);
  }

  #logo {
    width: 44px;
    height: 47px;
  }

  #theme-toggle {
    height: 32px;
  }

  #sun-svg {
    margin-top: 1px;
    font-size: 60px !important;
  }

  #moon-svg {
    width: 18px;
    height: 18px;
    margin-top: 1px;
  }

  #toggle-circle {
    top: 2px;
  }

  #hero-section section {
    width: calc(100% - 12px) !important;
  }

  #hero-section h3 {
    font-size: 24px !important;
  }

  #hero-section h2 {
    font-size: 34px !important;
  }

  #hero-section h1 {
    font-size: 36px !important;
  }

